import React from 'react'
import imagem from '../images/adm.png'

export default function Administrativo_atua() {
  return (
    <div className='flex items-center font-serif flex-col bg-color-white'>
        <div className='bg-color-white pt-12 pb-4 '>
            <h1 className='text-color-clicado font-bold text-4xl'>Direito Administrativo</h1>
        </div>
        <div className=' filter grayscale hidden lg:block bg-color-white'>
        <img className='w-full' src={imagem}></img>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>O que é direito Administrativo?</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O Direito Administrativo é uma área do direito público que se dedica ao estudo e regulamentação da administração pública. Ele abrange um conjunto de normas e princípios que governam a atuação dos órgãos estatais, agentes públicos e entidades governamentais.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Dentro do escopo do Direito Administrativo, uma área de grande importância diz respeito aos atos administrativos. Isso envolve a criação, validade e efeitos dos atos praticados pela administração pública, como decretos, portarias, decisões administrativas e regulamentos. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Além disso, o Direito Administrativo também trata da responsabilidade do Estado por danos causados a terceiros em decorrência da atuação dos seus agentes. Isso inclui situações como acidentes causados por servidores públicos em suas atividades funcionais. Há normas específicas para determinar quando o Estado é responsável e como os danos devem ser compensados.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'> Ele busca um equilíbrio entre o poder do governo e os interesses individuais e coletivos da sociedade, promovendo a transparência, legalidade e eficiência na gestão pública.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Serviço Público</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O serviço público é caracterizado por algumas características fundamentais. Uma delas é a sua finalidade pública, ou seja, ele é prestado visando ao interesse comum da sociedade e não a interesses privados. Além disso, o serviço público geralmente é prestado de forma contínua e regular, com a intenção de atender às necessidades da população de maneira constante e consistente.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Quando os serviços são delegados a entidades privadas, é comum a celebração de contratos de concessão ou permissão, que estabelecem as condições e obrigações para a prestação dos serviços.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Seja você uma entidade pública ou público-privada, atuamos como defensores dos interesses do governo em processos judiciais relacionados a questões administrativas, como disputas contratuais, responsabilidade civil do Estado ou contestações regulatórias.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Além disso, em casos de controvérsias entre cidadãos e o Estado, como questões envolvendo expropriação de propriedades ou disputas relacionadas a serviços públicos, representamos também os interesses dos cidadãos perante o Estado, buscando justiça e proteção de direitos individuais.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Licitações e Contratos Públicos</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>As licitações são procedimentos competitivos pelos quais a administração pública adquire bens, contrata serviços ou realiza obras. Elas são obrigatórias em grande parte das aquisições realizadas pelo governo, garantindo que diferentes empresas e fornecedores tenham a oportunidade de participar em igualdade de condições. O processo de licitação envolve a publicação de editais, a definição de critérios de seleção, a análise das propostas e a escolha do vencedor.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Uma vez concluído o processo de licitação, a administração pública celebra contratos com os fornecedores escolhidos. Esses contratos detalham as obrigações e direitos das partes envolvidas, estabelecendo prazos, condições de pagamento, penalidades e outras cláusulas relevantes.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Eles representam a base para aquisições e parcerias entre o governo e o setor privado, promovendo o interesse público e a qualidade na prestação de serviços e obras financiados pelos contribuintes.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Entre em contato conosco hoje mesmo e descubra como podemos ajudar a resolver seus problemas com licitações e contratos de forma eficiente e profissional. Juntos, vamos alcançar o sucesso que você merece!</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Regime Jurídico dos Servidores Públicos</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O Regime Jurídico dos Servidores Públicos é um conjunto de normas e princípios que regulam a relação entre o Estado e seus servidores. Ele é fundamentado em princípios como legalidade, impessoalidade, moralidade, publicidade e eficiência, buscando garantir a eficácia e a legalidade na administração pública. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Uma vez concluído o processo de licitação, a administração pública celebra contratos com os fornecedores escolhidos. Esses contratos detalham as obrigações e direitos das partes envolvidas, estabelecendo prazos, condições de pagamento, penalidades e outras cláusulas relevantes.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Oferecemos assessoria legal especializada, esclarecendo os direitos e deveres dos servidores públicos, interpretando leis e regulamentos específicos, e fornecendo orientações sobre questões relacionadas ao regime. Se você estiver enfrentando disputas, medidas disciplinares ou desafios em relação a sua carreira como servidor público, nossa equipe está pronta para representar seus interesses e defender seus direitos, garantindo que você receba o devido processo legal e justiça.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Desapropriação</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>A desapropriação é um instituto jurídico no âmbito do Direito Administrativo que permite que o Estado tome posse de propriedades privadas para fins de utilidade pública ou interesse social, mediante indenização justa ao proprietário. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Esse processo é regulado por leis específicas e envolve uma série de etapas, incluindo a declaração de utilidade pública ou interesse social, a avaliação do imóvel, a negociação com o proprietário e, se necessário, a ação judicial para determinar o valor da indenização.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Um dos princípios fundamentais da desapropriação é a garantia de justa indenização ao proprietário. Isso significa que o Estado deve pagar um valor equivalente ao real prejuízo financeiro que o proprietário sofre ao perder sua propriedade.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'> Se você é proprietário de um imóvel que está sujeito a um processo de desapropriação pelo Estado para fins de utilidade pública ou interesse social, ou se está envolvido em qualquer aspecto desse processo, estamos aqui para ajudar.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Além disso, podemos representá-lo em negociações com o Estado, buscando um acordo favorável e justo em relação à indenização. Se necessário, iremos à via judicial para garantir que você receba uma indenização adequada e que todos os aspectos legais sejam devidamente considerados.</h1>


        </div>
    </div>
  )
}
